<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Data Loan Iuran
      </div>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click="data.dialog = true" depressed elevation="2">
        <v-icon>mdi-upload</v-icon>
        Upload Data Iuran</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.firstname`]="{ item }">
          {{ item.firstname }} {{ item.lastname }}
        </template>
        <template #[`item.saldoiuranwajib`]="{ item }">
            {{ item.saldoiuranwajib > 0 ? toRupiah(item.saldoiuranwajib): '0'}}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn depressed elevation="2" color="primary" @click="hapus(item.id)">
            <v-icon small class="mr-2">
              mdi-delete
            </v-icon> Delete
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Upload/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import Upload from '@/components/DataIuran/Upload.vue'
import store from '@/store'
import Utils from '@/utils/util'
export default {
  name: 'DataIuranPage',
  components: {
    Upload
  },
  data: () => ({
    search: '',
    headers: [
      {
        sortable: false,
        text: '#',
        value: 'index'
      },
      { text: 'NIK', value: 'NIK', sortable: false },
      { text: 'Name', value: 'firstname', sortable: false },
      { text: 'Saldo Iuran Wajib', value: 'saldoiuranwajib', align: 'right', sortable: false },
      { text: 'Action', value: 'action', sortable: false }
    ]
  }),
  mounted: function () {
    store.dispatch('dataiuran/reqData')
  },
  computed: {
    ...mapState('dataiuran', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('dataiuran', [
      'hapus'
    ]),
    toRupiah (nominal) {
      return Utils.toRupiah(nominal)
    }
  }
}
</script>
